$time: 0.9s;

@keyframes in {
  0% {
    transform: scale(0.5);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes out {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(0.5);
  }
}

.content {
  animation-duration: $time;
  animation-iteration-count: 1;
  animation-name: in;
  animation-timing-function: ease-in-out;
  background-color: var(--bg-color);
  border-radius: 12px;
  padding: 20px;
  transition: opacity 0.3s ease-in-out;
}

.animationOut {
  animation-name: out;
}
